* {
    margin: 0px;
    box-sizing: border-box;
    padding: 0px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.head {
    font-size: 3rem;
}

.form-control {
    display: inline-block !important;
    width: 65%;
    font-size: 1.1rem;
    color: rgb(235, 177, 177);
    font-weight: 700;
}

#gg {
    border: 2px solid black;
    box-shadow: 0.5px 0.5px 10px rgba(0, 0, 0, 0.5);
    color: black !important;
}

.main {
    background-color: rgb(123, 145, 63);
    min-height: 100vh;
    background-size: cover;
}

.jii {
    margin-top: 20px;
    width: 30%;
    min-height: 80vh;
    border: 3px solid black;
    box-shadow: 0.5px 0.5px 10px rgba(0, 0, 0, 1);
    border-radius: 18px;
    background-color: transparent;
    padding-bottom: 20px;
    /* width: 70%; */
}

.order {
    margin-top: 3px;
    min-height: 480px;
    /* background-color: rgb(227, 238, 238); */
    border: 2px solid black;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 1);
    border-radius: 16px;
}

.ul {
    padding: 20px;
    margin-top: 10px;
    font-size: 1.5rem;
    text-align: start;
    list-style: none;
    min-height: 90%;
}

.list {
    text-transform: capitalize;
    list-style: none;
    border-radius: 10px;
    text-align: start;
    box-shadow: 0.5px 0.5px 10px rgba(0, 0, 0, 1);
    background-color: rgb(98, 82, 240);
    font-size: 1.6rem;
    font-weight: 770;
    margin: 11px 0px;
    padding: 12px 2px;
}

.hel {
    float: right;
    padding-right: 5px;
}

@media (max-width:1190px) {
    .jii {
        width: 50%
    }
    .list {
        font-size: 1.2rem;
    }
}

@media (max-width:890px) {
    .jii {
        width: 55%
    }
    .list {
        font-size: 1.2rem;
    }
}

@media (max-width:640px) {
    .jii {
        width: 85%
    }
    .list {
        font-size: 1rem;
    }
}

@media (max-width:490px) {
    .jii {
        width: 95%
    }
    .list {
        font-size: 1rem;
    }
}

@media (max-width:380px) {
    .jii {
        width: 110%;
        margin: 0px -10px;
    }
    .list {
        border-radius: 10px;
        text-align: start;
        box-shadow: 0.5px 0.5px 10px rgba(0, 0, 0, 1);
        /* background-color: rgb(95, 175, 206); */
        font-size: 1rem;
        font-weight: 700;
        margin: 11px -10px;
        padding: 12px 2px;
    }
    .order {
        min-height: 480px;
        margin: 0px -15px;
    }
    .hel {
        font-size: 1.3rem;
    }
}

.t {
    margin: 0px 2px;
}